@font-face {
  font-family: 'Karla';
  font-style: italic;
  font-weight: 400;
  src: local("Karla Italic"), local("Karla-Italic"), url("../fonts/karla-italic.woff2") format("woff2"), url("../fonts/karla-italic.woff") format("woff");
}

@font-face {
  font-family: 'Karla';
  font-style: normal;
  font-weight: 400;
  src: local("Karla"), local("Karla-Regular"), url("../fonts/karla-regular.woff2") format("woff2"), url("../fonts/karla-regular.woff") format("woff");
}

@font-face {
  font-family: 'Karla';
  font-style: italic;
  font-weight: 700;
  src: local("Karla Bold Italic"), local("Karla-BoldItalic"), url("../fonts/karla-bolditalic.woff2") format("woff2"), url("../fonts/karla-bolditalic.woff") format("woff");
}

@font-face {
  font-family: 'Karla';
  font-style: normal;
  font-weight: 700;
  src: local("Karla Bold"), local("Karla-Bold"), url("../fonts/karla-bold.woff2") format("woff2"), url("../fonts/karla-bold.woff") format("woff");
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-doc {
  width: 1em;
  height: 1em;
  fill: none;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-family: 'Karla', Arial, sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

.block {
  display: flex;
}

.title-lg {
  font-size: 36px;
  line-height: 1.16667;
}

.info-block {
  color: #fff;
  background: #000;
}

.info-block__header {
  margin: 0 auto;
  max-width: 970px;
  text-align: center;
}

.info-block__header p {
  font-size: 20px;
  line-height: 1.5;
}

.info-block__logo {
  display: inline-block;
  vertical-align: top;
  width: 48px;
}
