@function em($size, $context: 16) {
	@return ($size/$context) * 1em; }

@function img-url($image) {
	@return url('../img/#{$image}'); }

@mixin image($image) {
	background-image: img-url($image); }

@mixin trans($what: all, $dur: 0.2s, $easing: ease) {
	transition: $what $dur $easing; }

@mixin smooth-scroll {
	-webkit-overflow-scrolling: touch; }

@mixin smooth-font {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale; }

@mixin gpu {
	transform: translate3d(0,0,0); }

@mixin md {
	@include r(1023) {
		@content; } }

@mixin sm {
	@include r(767) {
		@content; } }

@mixin xs {
	@include r(599) {
		@content; } }

//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight) {
	font-family: 'Karla';
	font-style: $style;
	font-weight: $weight; }

@font-face {
	@include fontdefault(italic, 400);
	@include fontpath('Karla Italic', 'Karla-Italic', karla-italic); }
@font-face {
	@include fontdefault(normal, 400);
	@include fontpath('Karla', 'Karla-Regular', karla-regular); }
@font-face {
	@include fontdefault(italic, 700);
	@include fontpath('Karla Bold Italic', 'Karla-BoldItalic', karla-bolditalic); }
@font-face {
	@include fontdefault(normal, 700);
	@include fontpath('Karla Bold', 'Karla-Bold', karla-bold); }

