@mixin f($font-size: null, $line-height: null, $letter-spacing: null) {
	@if $font-size != null {
		font-size: $font-size * 1px; }
	@if $line-height != null {
		line-height: $line-height/$font-size; }
	@if $letter-spacing != null {
		letter-spacing: ($letter-spacing / 1000) * 1em; } }

@mixin reg {
	font-family: "America Regular", Helvetica Neue, Helvetica, Arial, sans-serif; }
@mixin bold {
	font-family: "America Bold", Helvetica Neue, Helvetica, Arial, sans-serif; }

